"use client";
import React, { useState, useCallback, ReactNode } from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";
import {
  YadakchiAdminLayout,
  ListPageLayout,
  Label,
  Badge,
  Button,
  FilterTabs,
  Select,
  type ColumnDefinition,
  type SelectOption,
} from "@my-ds/design-system";

import { getTickets, } from "../../services/api/ticketService";
import { Ticket } from "@/services/api/types";
import { useListPage } from "../../hooks/ListPageHook";
import { pageSizeOptions, commonTimeFilters } from "../../config/filters.config";
import { exportToExcel } from "../../utils/exportUtils";

// ุฏุงุฏูโูุง ููุชุฑ ูุถุนุช ูุฎุชุต ุชฺฉุชโูุง
const ticketStatusFilters: SelectOption[] = [
    { value: 'all', label: 'ููู ูุถุนุชโูุง' },
    { value: 'Open', label: 'ุจุงุฒ' },
    { value: 'Answered', label: 'ูพุงุณุฎ ุฏุงุฏู ุดุฏู' },
    { value: 'Closed', label: 'ุจุณุชู ุดุฏู' },
];

export default function TicketsPage() {
  const pathname = usePathname();
  const renderLink = (href: string, children: ReactNode) => <Link href={href}>{children}</Link>;

  // ุชุงุจุน ูุงฺฉุด ุฏุงุฏู ุฑุง ุจุง useCallback ูพุงุฏุงุฑ ูโฺฉูู
  const fetchTicketsData = useCallback((page: number, size: number, filters?: any) => {
    return getTickets(page, size);
  }, []);

  // ูุฑุงุฎูุงู ููฺฉ ููุดููุฏ ุจุง ูพฺฉุฑุจูุฏ ุจุฑุง ุชฺฉุชโูุง
  const {
    data: tickets,
    totalCount,
    isLoading,
    error,
    currentPage,
    pageSize,
    searchTerm,
    activeTimeFilter,
    selectedStatus,
    handlePageChange,
    handlePageSizeChange,
    handleSearchChange,
    handleTimeFilterChange,
    handleStatusChange,
    refreshData: refreshTickets,
  } = useListPage<Ticket>({
    fetchData: fetchTicketsData,
    searchKeys: ['title'],
    dateKey: 'createDate',
    statusKey: 'status',
  });
  
  // State ูุง ูุฎุชุต ุงฺฉุดูโูุง ุงู ุตูุญู
  const [isExporting, setIsExporting] = useState(false);

  // --- Handlers ---
  const handleViewTicket = (ticketId: number) => alert(`ูุดุงูุฏู ุชฺฉุช: ${ticketId}`);
  const handleEditTicket = (ticketId: number) => alert(`ูุฑุงุด ุชฺฉุช: ${ticketId}`);
  const handleDeleteTicket = (ticketId: number) => alert(`ุญุฐู ุชฺฉุช: ${ticketId}`);
  
  const handleExport = async () => {
    if (!tickets.length) return;
    setIsExporting(true);
    await new Promise(resolve => setTimeout(resolve, 500));
    const dataToExport = tickets.map(t => ({
      'ุดูุงุณู': t.id,
      'ููุถูุน': t.title,
      'ูุถุนุช': t.status,
      'ุงูููุช': t.priority,
      'ุชุงุฑุฎ ุซุจุช': new Date(t.createDate).toLocaleDateString('fa-IR'),
    }));
    exportToExcel(dataToExport, "ูุณุช_ุชฺฉุชโูุง");
    setIsExporting(false);
  };

  // --- Column Definitions ---
  const ticketColumns: ColumnDefinition<Ticket>[] = [
    {
      accessorKey: 'id',
      header: 'ุฑุฏู',
      cell: (ticket, index) => <Label text={String((currentPage - 1) * pageSize + index + 1)} size="md" />,
    },
    { accessorKey: 'title', header: 'ููุถูุน ุชฺฉุช', cell: (t) => <Label text={t.title} size="md" /> },
    { 
      accessorKey: 'status', 
      header: 'ูุถุนุช', 
      cell: (t) => <Badge label={t.status} variant={t.status === 'Open' ? 'success' : (t.status === 'Answered' ? 'info' : 'neutral')} /> 
    },
    { 
      accessorKey: 'priority', 
      header: 'ุงูููุช', 
      cell: (t) => <Label text={t.priority} size="md" /> 
    },
  ];
  
  // --- Props Preparation ---
  const filterSection = (
    <div style={{ display: 'flex', flexWrap: 'wrap', gap: '1rem', alignItems: 'center' }}>
      <FilterTabs
        tabs={commonTimeFilters}
        activeTab={activeTimeFilter}
        onTabChange={handleTimeFilterChange}
      />
      <Select
        options={ticketStatusFilters}
        value={String(selectedStatus)}
        onChange={handleStatusChange}
        aria-label="ููุชุฑ ุจุฑ ุงุณุงุณ ูุถุนุช"
      />
    </div>
  );
  
  const listPageProps = {
    listPageHeaderProps: {
      title: "ูุฏุฑุช ุชฺฉุชโูุง",
      totalItems: totalCount,
      searchInputProps: {
        value: searchTerm,
        onChange: handleSearchChange,
        placeholder: "ุฌุณุชุฌู ุฏุฑ ููุถูุนุงุช...",
      },
      exportButtonProps: { onClick: handleExport, isLoading: isExporting },
      actions: <Button>ุงุฌุงุฏ ุชฺฉุช ุฌุฏุฏ</Button> 
    },
    tableProps: {
      columns: ticketColumns,
      data: tickets,
      emptyStateMessage: "ูฺ ุชฺฉุช ุงูุช ูุดุฏ.",
      rowActions: (ticket: Ticket) => ({
        onView: () => handleViewTicket(ticket.id),
        onEdit: () => handleEditTicket(ticket.id),
        onDelete: () => handleDeleteTicket(ticket.id),
      }),
    },
    paginationProps: {
      currentPage: currentPage,
      pageSize: pageSize,
      totalCount: totalCount,
      onPageChange: handlePageChange,
      pageSizeOptions: pageSizeOptions,
      onPageSizeChange: handlePageSizeChange,
    },
    filterSection: filterSection,
    isLoading: isLoading,
    error: error,
  };

  // --- JSX ---
  return (
    <YadakchiAdminLayout
      currentPath={pathname}
      renderLink={renderLink}
    >
      <ListPageLayout {...listPageProps} />
    </YadakchiAdminLayout>
  );
}